* {
    margin: 0;
    padding: 0;
}

article {
    width: 60%;
    margin: auto;
}

.cart_box img {
    width: 70px;
    height: 70px;

}

.cart_img {
    display: flex;
    width: 400px;

}

.cart_img p {
    font-weight: bold;
    margin-left: 10px;
}

.cart_box {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px;
    border-bottom: 2px solid teal;
    padding-bottom: 10px;
}

.cart_box div:nth-child(2) button {
    padding: 5px 10px;
    font-weight: bold;
    margin-right: 5px;
}

.cart_box div:nth-child(3) button {
    padding: 5px 10px;
    font-weight: bold;
    letter-spacing: 2px;
    border: none;
    outline: none;
    color: white;
    background-color: teal;
    border-radius: 5px;
    cursor: pointer;
    margin-left: 10px;
}

.cart_box div:nth-child(3) button:hover {
    /* background-color: darkseagreen; */
    background-color: black;
}

.cart_box div:nth-child(3) span {
    /* background-color: lightgreen; */
    background-color: gray;
    color: white;
    padding: 5px 10px;
    border-radius: 5px;
    margin-right: 10px;
}

.total {
    display: flex;
    justify-content: space-between;
}

.total span:nth-child(1) {
    font-size: 2rem;
    color: black;
    font-weight: bold;
    letter-spacing: 2px;
}

.total span:nth-child(2) {
    font-size: 2rem;
    color: white;
    background-color: black;
    font-weight: bold;
    border: 1px solid black;
    border-radius: 10px;
    margin: 10px;
    padding: 5px 10px;
}